import { Injectable } from '@angular/core';
import { TsCategorie} from './usCategorie';

@Injectable()
export class TeCategorie
  {
  id: number;
      Symbol: string;
    Description: string;
  // champs calculs (supprims dans to_ServerValue() )
  SID: string= '';
  modifie: Boolean= false;
  service: TsCategorie= null;

  public static id_parameter( _id: number) { return /*'id=' +*/ _id; }

  constructor(values: Object = {})
    {
    Object.assign(this, values);
    }
  public Valide(): void
    {
    if (!this.service) { return; }
    this.service.Set( this)
    .then( _e => { Object.assign(this, _e); });
    }
  public to_ServerValue(): TeCategorie
    {
    const Result: TeCategorie= new TeCategorie( this);

    delete Result.SID;
    delete Result.service;
    delete Result.modifie;

    return Result;
    }
  }
